<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div class="back">
    <div class="kong"></div>
    <div class="container">
      <div class="row">
        <div class="media col-xs-12">
          <div class="media-left">
            <a href="#">
              <img
                class="media-object cover"
                :src="this.url + specific.cover"
                height="300"
              />
            </a>
          </div>
          <div class="media-body">
            <h3>{{ specific.name }}</h3>
            <h5>{{ specific.language }}</h5>
            <h5>{{ specific.duration }}分钟</h5>
            <h5>{{ specific.playDate }} 中国大陆上映</h5>
            <div class="below">
              <div class="left">
                <button type="button" class="btn btn-default">
                  <i class="glyphicon glyphicon-heart"></i>想看
                </button>
                <button type="button" class="btn btn-default">
                  <i class="glyphicon glyphicon-star"></i>评分
                </button>
                <p class="buy">
                  <button type="button" class="btn btn-danger">特惠购票</button>
                </p>
              </div>
              <div class="right">
                <span>猫眼口碑</span>
                <h4 class="text-warning">{{ specific.score }}.0</h4>
                <span>累计票房</span>
                <h4>{{ specific.favoriteNum }}</h4>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="col-xs-8 col-xs-offset-1">
    <h4 class="line">剧情简介</h4>
    <div class="blurb" v-html="specific.introduction"></div>
    <h4 class="line">评论</h4>
    <div class="remark">
      <comment :specific="specific.id"></comment>
    </div>
  </div>
</template>

<script>
import Comment from "./comment.vue";
export default {
  components: {
    Comment,
  },
  data() {
    return {
      specific: {},
      url: "http://124.93.196.45:10001/",
    };
  },
  created() {
    this.axios({
      method: "get",
      url: this.$route.fullPath,
    })
      .then((response) => {
        if (response.status === 200) {
          this.specific = response.data.data;
          // console.log(response.data.data);
        }
      })
      .catch((error) => console.log(error));
  },
};
</script>

