<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div v-if="lists">
    <div class="media" v-for="(i, index) in lists.slice(0, 5)" :key="index">
      <div class="media-left">
        <i class="glyphicon glyphicon-user" style="font-size: 30px"></i>
      </div>
      <div class="media-body">
        <h5 class="media-heading">
          {{ i.nickName }}
          <small>{{ i.commentDate }}发布</small>
        </h5>
        <p v-html="i.content"></p>
        <p>
          <span @click="like(i.id)">
            <i class="glyphicon glyphicon-thumbs-up"></i>{{ i.likeNum }}
          </span>
          |<a href="#"> 分享 </a>
        </p>
      </div>
    </div>
  </div>
  <div v-else class="text-center">
    <h3>暂无评论</h3>
  </div>
</template>

<script>
export default {
  props: ["specific"],
  data() {
    return {
      lists: [],
      url: "http://124.93.196.45:10001/",
      item: "",
    };
  },
  methods: {
    like(id) {
      console.log(window.sessionStorage["Authorization"]);
      this.axios({
        method: "post",
        url: "/prod-api/api/movie/film/comment/like/"+id,
        // data: { id: this.id},
        headers: {
          Authorization: window.sessionStorage["Authorization"],
        },
      })
      .then((resopnse) => {
        if (resopnse.status == 200) {
          if (resopnse.data.code == 200) {
            console.log(resopnse.data);
            alert("点赞成功");
            // this.$router.go(0);
          } else {
            alert("请先登录");
            console.log(resopnse.data);
          }
        } else {
          console.log(resopnse.data);
        }
      })
      .catch((error) => {
        console.log(error);
      });
    },
  },
  beforeUpdate() {
    console.log(this.specific);
    this.axios({
      method: "get",
      url: "/prod-api/api/movie/film/comment/list?movieId=" + this.specific,
    })
      .then((response) => {
        if (response.status === 200) {
          this.lists = response.data.rows;
          // console.log(response.data);
        } else {
          console.log(response.data);
        }
      })
      .catch((error) => console.log(error));
  },
};
</script>

